import './courses.scss';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Ribbon from '../../components/ribbon/ribbon';
import classnames from 'classnames';

const CoursesContainer = (props) => {
  const half = Math.ceil(props.data.courses.length / 2);
  return (
    <div className="container-courses animate__fade-in-down">
      <div className="container-courses__column">
        {[...Array(half).keys()].map((i) => (
          <Ribbon key={i} type="right" title={props.data.courses[i].subject + ' ' + props.data.courses[i].code}>
            {/* prevent whitespace wrapping of (i) icon at end of course name by grouping desired words into 'blocks' with white-space: no-wrap */}
            {props.data.courses[i].title.split(' ').map((word, i, arr) => (
              <span key={i} className="format__textblock">{word}{i === arr.length - 1 && <FontAwesomeIcon className="themify--active-color-hover" icon={['fas', 'info-circle']} />}</span>
            ))}
          </Ribbon>
        ))}
      </div>
      <div className="container-courses__column">
        {[...Array(props.data.courses.length - half).keys()].map((i) => (
          <Ribbon key={i} type="right" title={props.data.courses[half + i].subject + ' ' + props.data.courses[half + i].code}>
            {/* prevent whitespace wrapping of (i) icon at end of course name by grouping desired words into 'blocks' with white-space: no-wrap */}
            {props.data.courses[half + i].title.split(' ').map((word, i, arr) => (
              <span key={i} className="format__textblock">{word}{i === arr.length - 1 && <FontAwesomeIcon className="themify--active-color-hover" icon={['fas', 'info-circle']} />}</span>
            ))}
          </Ribbon>
        ))}
      </div>
    </div>
  );
};

export default CoursesContainer;