import './activity.scss';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Ribbon } from '../../components/components';

const ActivityContainer = (props) => (
  <div className="container-activity animate__fade-in-down">
    {props.data.projects.map((project) => (
        <div className="container-activity__row">
          <Ribbon type="right" title={project.title}>
            {project.latest}
            <FontAwesomeIcon className="themify--active-color-hover" icon={[project.icon.style || 'fas', project.icon.name]} />
          </Ribbon>
          <p className="container-activity__description">{project.description}</p>
        </div>
      ))}
  </div>
);

export default ActivityContainer;