import './portfolio.scss';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classnames from 'classnames';

class PortfolioContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activeId: 0 };
    this.nextItem = this.nextItem.bind(this);
    this.prevItem = this.prevItem.bind(this);
  }

  nextItem() {
    if (this.state.activeId < this.props.data.projects.length - 1) this.setState({ activeId: this.state.activeId + 1 });
  }

  prevItem() {
    if (this.state.activeId > 0) this.setState({ activeId: this.state.activeId - 1 });
  }

  selectItem(id) {
    if (id !== this.state.activeId) this.setState({ activeId: id });
  }

  render() {
    return (
      <div className="container-portfolio">
        <div className="carousel">
          <table className="carousel__display">
            <tr>
              <td>
                <img className="carousel__img" alt={this.props.data.projects[this.state.activeId].title} src={this.props.data.projects[this.state.activeId].thumbnail} />
              </td>
              <td>
                <div className="carousel__details">
                  <div className="carousel__title">
                    {this.props.data.projects[this.state.activeId].title}
                    <div className="carousel__links">
                      {this.props.data.projects[this.state.activeId].links.map((link) => (
                        <a className="carousel__link" href={link.url}>
                          <FontAwesomeIcon className="themify--active-color-hover" icon={[link.icon.style, link.icon.name]} />
                        </a>
                      ))}
                    </div>
                  </div>
                  <div className="carousel__description">
                    <div className="carousel__text">
                      {this.props.data.projects[this.state.activeId].description}
                    </div>
                    <div className="carousel__tools">
                      {this.props.data.projects[this.state.activeId].tools.map((tool, key) => (
                        <div key={key} className="carousel__tool themify--active-background">{tool}</div>
                      ))}
                    </div>
                  </div>
                </div>
              </td>
            </tr>
          </table>
          {/* </div> */}
          <div className="carousel__controls">
            <div className="carousel__next themify--inactive-background-hover" onClick={this.prevItem}>
              <FontAwesomeIcon icon={['fas', 'chevron-left']} />
            </div>
            {this.props.data.projects.map((project, key) => (
              <div key={project.name} className={classnames("carousel__item", this.state.activeId === key ? "themify--active-background" : "themify--inactive-background-hover")} onClick={() => this.selectItem(key)}></div>
            ))}
            <div className="carousel__back themify--inactive-background-hover" onClick={this.nextItem}>
              <FontAwesomeIcon icon={['fas', 'chevron-right']} />
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default PortfolioContainer;