import './theme.scss';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Theme as ThemeModel } from '../../models/models';
import classnames from 'classnames';
import Particles from 'react-particles-js';
import particlesData from './particles-front.json';
import Parallax from 'parallax-js';

const ANIMATION_TIME = 400;

/**
 * Renders panel background component
 * @param {*} props - the component's properties
 * 
 * usage: <Theme ... />
 */
class Theme extends React.Component {
  _intersectObserver = null;
  _navObserver = null;

  _clickOberserver = null;
  _hoverOberserver = null;
  _hovered = null;
  _clicked = null;

  constructor(props) {
    super(props);
    this.state = { animating: false, nav: { down: true, up: false }, transforms: { background: null, foreground: null }, theme: { current: ThemeModel.get(0), next: {}, hover: {} } };
    this.updateUI = this.updateUI.bind(this);
    this.triggerClick = this.triggerClick.bind(this);
    this.triggerHover = this.triggerHover.bind(this);
    this.refParallax = React.createRef();
    this.updateCurrent = this.updateCurrent.bind(this);
    this.updateNav = this.updateNav.bind(this);
    // check if user is a mobile device
    this._isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  }

  componentDidMount() {
    this._intersectObserver = ThemeModel.intersectObservable().subscribe({next: this.updateCurrent});
    this._navObserver = ThemeModel.navObservable().subscribe({next: this.updateNav});

    this.parallax = new Parallax(this.refParallax.current);
    window.addEventListener('resize', this.updateUI);
    this.updateUI();
    this._clickOberserver = ThemeModel.clickObservable().subscribe({next: this.triggerClick});
    this._hoverOberserver = ThemeModel.hoverObservable().subscribe({next: this.triggerHover});
  }

  componentWillUnmount() {
    if (this._intersectObserver) this._intersectObserver.unsubscribe();

    this.parallax.disable();
    window.removeEventListener('resize', this.updateUI);
    if (this._clickOberserver) this._clickOberserver.unsubscribe();
    if (this._hoverOberserver) this._hoverOberserver.unsubscribe();
  }

  updateCurrent(theme) {
    // update currently displayed theme colors
    // console.log(theme.primary, theme.secondary);
    this.setState({...this.state, theme: {...this.state.theme, current: {...theme}}});
  }

  updateNav(nav) {
    if (nav.up !== this.state.nav.up || nav.down !== this.state.nav.down) {
      this.setState({...this.state, nav});
    }
  }

  triggerClick({id, theme}) {
    // console.log('a');
    this._clicked = {id, theme: this.state.theme.next};
    // do nothing if timeout is active
    if (this._timeout) return;
    //
    if (this.state.animating) {
      // console.log('b');
      this._timeout = setTimeout(() => {
        // console.log(this._clicked);
        this.setState({...this.state, animating: false, theme: {...this.state.theme, current: {...this._clicked.theme}, next: {}, hover: {}}});
        this._clicked = null;
        this._timeout = null;
      }, ANIMATION_TIME - Math.min(Date.now() - this._animationStart, ANIMATION_TIME))
    }
    // otherwise set new current theme
    else {
      // console.log('c');
      this.setState({...this.state, animating: false, theme: {...this.state.theme, current: {...this._clicked.theme}, next: {}, hover: {}}});
      this._clicked = null;
    }
  }

  triggerHover({id, theme}) {
    // do nothing if timeout is active
    if (this._timeout) {
      // console.log('d');
      this.setState({...this.state, theme: {...this.state.theme, hover: {...theme}}});
      this._hovered = {id, theme};
    }
    // perform transition animation
    else if (!this.state.animating) {
      // console.log('e');
      // if (ThemeModel.isDefault(theme)) return this.setState({...this.state, animating: false, theme: {...this.state.theme, next: {...theme}, hover: {...theme}}});
      this._animationStart = Date.now();
      this.setState({...this.state, animating: !ThemeModel.isNone(theme) && !ThemeModel.isEqual(theme, this.state.theme.current), theme: {...this.state.theme, next: {...theme}, hover: {...theme}}});
    }
    // stop animation and set timeout
    else {
      // console.log('f');
      this.setState({...this.state, theme: {...this.state.theme, hover: {...theme}}});
      this._timeout = setTimeout(() => {
        // console.log('g');
        if (this._clicked) {
          // console.log('h');
          this._hovered = null;
          this._timeout = null;
          this.triggerClick({id});
          return;
        }
        this.setState({...this.state, animating: false});
        this._timeout = setTimeout(() => {
          // console.log('i');
          const next = this._hovered;
          this._hovered = null;
          this._timeout = null;
          if (next) this.triggerHover(next);
        }, ANIMATION_TIME);
      }, ANIMATION_TIME - Math.min(Date.now() - this._animationStart, ANIMATION_TIME));
    }
  }

  updateUI() {
    this.updateBackground();
  }

  updateBackground() {
    // if only height has changed and user is using mobile device, do nothing
    // NOTE: this is a workaround for the mobile address bars modifying window.innerHeight
    if (this._prevWidth === window.innerWidth && this._isMobile) return;
    const offsetY = window.innerHeight / 2;
    const angle = -Math.atan(window.innerHeight / window.innerWidth) * (180 / Math.PI);
    this.setState({ transforms: {background: `translateY(${offsetY}px) skewY(${angle}deg)`, foreground: `translateY(${-offsetY}px) skewY(${-angle}deg)`}});
    this._prevWidth = window.innerWidth;
  }

  render() {
    return (
      <div className="panel-background-primary" style={{ backgroundColor: this.state.theme.current.primary }}>
        <style dangerouslySetInnerHTML={{ __html: `.toggles__toggle--sm.toggles__toggle--active { background-color: ${this.state.theme.current.primary}; } .toggles__toggle--hover.toggles__toggle--lg .ribbon > div, .toggles__toggle--hover.toggles__toggle--lg .ribbon .ribbon__left, .toggles__toggle--hover.toggles__toggle--lg .ribbon__right, .toggles__toggle--hover.toggles__toggle--sm { background-color: ${this.state.theme.hover.secondary}; } .toggles__toggle--hover.toggles__toggle--lg:not(.toggles__toggle--animating) svg { color: ${this.state.theme.hover.secondary}; } .themify--active-color, .themify--active-color-hover {color: ${this.state.theme.current.primary};} .themify--active-color-hover:hover {color: ${this.state.theme.current.secondary};} .themify--active-background {background-color: ${this.state.theme.current.primary};} .themify--active-background-hover:hover, .themify--inactive-background-hover:hover {background-color: ${this.state.theme.current.secondary};}` }} />
        <div className={classnames('panel-background-secondary', this.state.animating && 'animating')} style={{ backgroundColor: this.state.theme.current.secondary, transform: this.state.transforms.background }}>
          <div className="panel-background-secondary-top" style={{ backgroundColor: this.state.theme.next.primary }}></div>
          <div className="panel-background-secondary-bottom" style={{ backgroundColor: this.state.theme.next.secondary }}></div>
          <div style={{ transform: this.state.transforms.foreground }}>
            <span className={classnames('panel-nav', 'panel-nav-up', !this.state.nav.up && 'hidden')}>
              <FontAwesomeIcon icon={['fas', 'chevron-up']} />
            </span>
            <span className={classnames('panel-nav', 'panel-nav-down', !this.state.nav.down && 'hidden')}>
              <FontAwesomeIcon icon={['fas', 'chevron-down']} />
            </span>
            <div ref={this.refParallax}>
              <div data-depth="0.3">
                <Particles data-invert-y="true" data-invert-x="true" params={particlesData} />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default Theme;